<template>
    <div class="m-fb-drop" v-loading="loading">
        <el-tabs v-model="mapid" type="card" @tab-click="loadData">
            <el-tab-pane
                :label="item.mode"
                :name="item.map_id"
                v-for="(item, i) in maplist"
                :key="i"
            >
                <div class="u-bosslist">
                    <!-- <div class="u-label">首领</div> -->
                    <div
                        class="u-boss"
                        :class="{ on: item.BossIndex == bossid }"
                        v-for="(item, j) in bosslist"
                        :key="j"
                        @click="loadDropList(item.BossIndex)"
                    >
                        <!-- <img :src="bossavatar(j)" /> -->
                        <svg xmlns="http://www.w3.org/2000/svg" width="833.7" height="833.7" viewBox="0 0 833.7 833.7" v-bind:class="'u-boss-avatar'" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path d="M416.8 0C208.5.1 39.6 169 39.6 377.399V555.2c0 9.399 4.4 18.199 11.9 23.899L213.7 702.2v101.5c0 16.6 13.4 30 30 30h24.1c16.6 0 30-13.4 30-30v-41.2c0-10.5 8.5-19 19-19s19 8.5 19 19v41.2c0 16.6 13.4 30 30 30h1c16.6 0 30-13.4 30-30v-41.2c0-10.5 8.5-19 19-19h2c10.5 0 19 8.5 19 19v41.2c0 16.6 13.4 30 30 30h1c16.6 0 30-13.4 30-30v-41.2c0-10.5 8.5-19 19-19s19 8.5 19 19v41.2c0 16.6 13.4 30 30 30H590c16.6 0 30-13.4 30-30V702.1L782.2 579c7.5-5.7 11.899-14.5 11.899-23.9V377.3C794 169 625.1.1 416.8 0zM259.1 558.1c-57.4 0-104-46.6-104-104 0-57.399 46.6-104 104-104s104 46.601 104 104c0 57.5-46.6 104-104 104zm187.3 76.5h-59.1c-13.7 0-22.4-14.7-15.8-26.7l29.6-53.699c6.8-12.4 24.7-12.4 31.5 0l29.601 53.699c6.5 11.999-2.201 26.7-15.801 26.7zm128.1-76.5c-57.4 0-104-46.6-104-104 0-57.399 46.6-104 104-104 57.399 0 104 46.601 104 104 0 57.5-46.5 104-104 104z"/></svg>
                        {{ item.BOSS }}
                    </div>
                </div>
                <div class="u-droplist">
                    <div class="u-tabs">
                        <el-tabs
                            tab-position="left"
                            class="u-group"
                            v-for="(group, groupname) in fillDroplist(droplist)"
                            :key="groupname"
                            v-model="activeType"
                        >
                            <el-tab-pane
                                :label="dropType(groupname)"
                                v-if="groupname !== 'maps'"
                                :name="groupname"
                            ></el-tab-pane>
                        </el-tabs>
                    </div>
                    <div class="u-drops">
                        <div class="u-drop" v-for="(group, groupname) in droplist" :key="groupname">
                            <div
                                v-show="
                                    activeType == 'all' ||
                                        groupname == activeType
                                "
                            >
                                <template v-if="groupname !== 'maps'">
                                    <div class="u-drop-wrapper" v-if="group && group.length">
                                        <div
                                            class="u-item"
                                            v-for="(drop, dropindex) in group"
                                            :key="dropindex"
                                        >
                                            <el-popover
                                                placement="top"
                                                width="200"
                                                trigger="hover"
                                                :open-delay="100"
                                                :close-delay="50"
                                            >
                                                <drop_item
                                                    class="u-item-content"
                                                    :data="drop"
                                                    :type="groupname"
                                                ></drop_item>
                                                <div slot="reference">
                                                    <i
                                                        class="u-drop-item-icon u-item-icon"
                                                        :class="
                                                            'u-item-color-' +
                                                                drop.Color
                                                        "
                                                    >
                                                        <img
                                                            v-if="
                                                                drop.Icon &&
                                                                    drop.Icon
                                                                        .FileName
                                                            "
                                                            :src="
                                                                drop.Icon
                                                                    .FileName
                                                            "
                                                        />
                                                        <img v-else src="../assets/img/null.png" />
                                                    </i>
                                                    <span class="u-item-name">
                                                        {{
                                                        drop.Name
                                                        }}
                                                    </span>
                                                </div>
                                            </el-popover>
                                        </div>
                                    </div>
                                    <el-alert
                                        v-if="
                                            activeType != 'all' &&
                                                (!group ||
                                                    (group && !group.length))
                                        "
                                        title="没有相关条目"
                                        type="info"
                                        show-icon
                                    ></el-alert>
                                </template>
                            </div>
                        </div>
                    </div>
                </div>
            </el-tab-pane>
        </el-tabs>
    </div>
</template>

<script>
import { getDrop, getMapid, getBossid } from "../service/getDrop";
import dropmap from "../assets/js/drop.json";
import { __iconPath, __ossRoot } from "@jx3box/jx3box-common/data/jx3box.json";
import drop_item from "../components/drop_item";
import _ from "lodash";
const iconids = [4835, 8848, 10452, 10451, 2589, 2646, 2647, 2648];

export default {
    name: "Drop",
    props: [],
    data: function () {
        return {
            mapid: "",
            bossid: "",
            bosslist: [],
            droplist: [],
            activeType: "all",
            data: {},
            loading: false,
        };
    },
    computed: {
        fb: function () {
            return this.$route.query.fb_name || this.$store.state.default_fb;
        },
        zlp: function () {
            return this.$route.query.fb_zlp || this.$store.state.default_zlp;
        },
        maplist: function () {
            return (
                this.$store.state.map &&
                this.$store.state.map[this.zlp]["dungeon"][this.fb]["maps"]
            );
        },
    },
    methods: {
        loadBossList: function () {
            return getBossid(this.mapid).then((data) => {
                this.bosslist = data;
                this.bossid = data[0]["BossIndex"];
            });
        },
        loadDropList: function (id) {
            this.bossid = id;
            this.loading = true;
            return getDrop(this.bossid)
                .then((data) => {
                    this.droplist = data && data.data;
                })
                .finally(() => {
                    this.loading = false;
                });
        },
        dropType: function (val) {
            return dropmap[val];
        },
        bossavatar: function (i) {
            return __iconPath + "icon/" + iconids[i] + ".png";
        },
        fillDroplist: function (list) {
            if (list) list["all"] = [];
            return list;
        },
        showDrop: function (drop) {
            this.data = drop;
        },
        loadData: function () {
            this.loadBossList().then(() => {
                this.loadDropList(this.bossid);
            });
        },
    },
    watch: {
        fb: {
            immediate: true,
            handler: function () {
                this.mapid = this.maplist && this.maplist[0]["map_id"];
                this.loadData();
            },
        },
    },
    components: {
        drop_item,
    },
};
</script>

<style lang="less">
@import "../assets/css/drop.less";
</style>
